import React, { useState } from "react";
import { useNFTBalances } from "react-moralis";
import { Card, Image, Tooltip, Modal, Input, Skeleton, Button } from "antd";
import { FileSearchOutlined, ShoppingCartOutlined } from "@ant-design/icons";
import { getExplorer } from "helpers/networks";
// import AddressInput from "./AddressInput";
import { useMoralisDapp } from "providers/MoralisDappProvider/MoralisDappProvider";
import { useVerifyMetadata } from "hooks/useVerifyMetadata";
// import { useEffect } from "react/cjs/react.production.min";
import { useWeb3ExecuteFunction } from "react-moralis";

const { Meta } = Card;

const styles = {
  NFTs: {
    display: "flex",
    flexWrap: "wrap",
    WebkitBoxPack: "start",
    justifyContent: "flex-start",
    margin: "0 auto",
    maxWidth: "1000px",
    width: "100%",
    gap: "10px",
  },
};

function NFTBalance() {
  const { data: NFTBalances } = useNFTBalances();
  // const { Moralis } = useMoralis();
  const [visible, setVisibility] = useState(false);
  // const [receiverToSend, setReceiver] = useState(null);
  // const [amountToSend, setAmount] = useState(null);
  // const [nftToSend, setNftToSend] = useState(null);
  const [price, setPrice] = useState();
  // const [isPending, setIsPending] = useState(false);
  const { verifyMetadata } = useVerifyMetadata();
  const [nftToSell, setNftToSell] = useState({});
  const { chainId, marketAddress, contractABI } = useMoralisDapp();
  const contractABIJson = JSON.parse(contractABI);
  const listItemFunction = "createMarketItem";
  const [loading, setLoading] = useState(false);
  const contractProcessor = useWeb3ExecuteFunction();

  // async function transfer(nft, amount, receiver) {
  //   const options = {
  //     type: nft.contract_type,
  //     tokenId: nft.token_id,
  //     receiver: receiver,
  //     contractAddress: nft.token_address,
  //   };

  //   if (options.type === "erc1155") {
  //     options.amount = amount;
  //   }

  //   setIsPending(true);
  //   await Moralis.transfer(options)
  //     .then((tx) => {
  //       console.log(tx);
  //       setIsPending(false);
  //     })
  //     .catch((e) => {
  //       alert(e.message);
  //       setIsPending(false);
  //     });
  // }
  const handleSellClick = (nft) => {
    setNftToSell(nft);
    setVisibility(true)
  }

  // const handleTransferClick = (nft) => {
  //   setNftToSend(nft);
  //   setVisibility(true);
  // };

  // const handleChange = (e) => {
  //   setAmount(e.target.value);
  // };

  async function approveAll(nft) {
    setLoading(true);  
    const ops = {
      contractAddress: nft.token_address,
      functionName: "setApprovalForAll",
      abi: [{"inputs":[{"internalType":"address","name":"operator","type":"address"},{"internalType":"bool","name":"approved","type":"bool"}],"name":"setApprovalForAll","outputs":[],"stateMutability":"nonpayable","type":"function"}],
      params: {
        operator: marketAddress,
        approved: true
      },
    };

    await contractProcessor.fetch({
      params: ops,
      onSuccess: () => {
        console.log("Approval Received");
        setLoading(false);
        setVisibility(false);
        succApprove();
      },
      onError: (error) => {
        setLoading(false);
        failApprove();
      },
    });
  }

  function succApprove() {
    let secondsToGo = 5;
    const modal = Modal.success({
      title: "Success!",
      content: `Approval is now set, you may list your NFT`,
    });
    setTimeout(() => {
      modal.destroy();
    }, secondsToGo * 1000);
  }

  function failApprove() {
    let secondsToGo = 5;
    const modal = Modal.error({
      title: "Error!",
      content: `There was a problem with setting approval`,
    });
    setTimeout(() => {
      modal.destroy();
    }, secondsToGo * 1000);
  }

  const list = async (nft, currentPrice) => {
    setLoading(true);
    const p = currentPrice * ("1e" + 18);
    const ops = {
      contractAddress: marketAddress,
      functionName: listItemFunction,
      abi: contractABIJson,
      params: {
        nftContract: nft.token_address,
        tokenId: nft.token_id,
        price: String(p),
      },
    };

    console.log(">>>>>>",ops);
    await contractProcessor.fetch({
      params: ops,
      onSuccess: () => {
        console.log("success");
        setLoading(false);
        setVisibility(false);
        // addItemImage();
        // succList();
      },
      onError: (error) => {
        setLoading(false);
        // failList();
      },
    });
  
  }
  

  console.log("NFTBalances", NFTBalances);
  return (
    <div style={{ padding: "15px", maxWidth: "1030px", width: "100%" }}>
      <h1>🖼 NFT Balances</h1>
      <div style={styles.NFTs}>
        <Skeleton loading={!NFTBalances?.result || loading}>
          {NFTBalances?.result &&
            NFTBalances.result.map((nft, index) => {
              //Verify Metadata
              nft = verifyMetadata(nft);
              return (
                <Card
                  hoverable
                  actions={[
                    <Tooltip title="View On Blockexplorer">
                      <FileSearchOutlined
                        onClick={() => window.open(`${getExplorer(chainId)}address/${nft.token_address}`, "_blank")}
                      />
                    </Tooltip>,
                    // <Tooltip title="Transfer NFT">
                    //   <SendOutlined onClick={() => handleTransferClick(nft)} />
                    // </Tooltip>,
                    <Tooltip title="Sell On OpenSea">
                      <ShoppingCartOutlined onClick={() => handleSellClick(nft)} />
                    </Tooltip>,
                  ]}
                  style={{ width: 240, border: "2px solid #e7eaf3" }}
                  cover={
                    <Image
                      preview={false}
                      src={nft?.image || "error"}
                      fallback="data:image/png;base64,"
                      alt=""
                      style={{ height: "240px" }}
                    />
                  }
                  key={index}
                >
                  <Meta title={nft.name} description={nft.token_address} />
                </Card>
              );
            })}
        </Skeleton>
      </div>
      <Modal
        title={`Sell ${nftToSell?.name || "NFT"}`}
        visible={visible}
        // onCancel={() => setVisibility(false)}
        // onOk={() => list(nftToSell, price)}
        // okText="Sell"
        footer={[
          <Button key="1" onClick={() => setVisibility(false)}>Cancel</Button>,
          <Button key="2" type="primary" onClick={() => approveAll(nftToSell)}>Approve</Button>,
          <Button key="3" type="primary" onClick={() => list(nftToSell, price)}>Sell</Button>
        ]}
      >
        <img
          alt="Sell NFT" 
          src={nftToSell.image}
          style={{
            width: "250px",
            margin: "auto",
            borderRadius: "10px",
            marginBottom: "15px"
          }}
        />
      <Input autoFocus placeholder="Set Price in BNB" onChange={(e) => setPrice(e.target.value)}/>
      </Modal>
    </div>
  );
}

export default NFTBalance;
